import { locations, vegetables } from "./data";
const responseOptions = { status: 200, statusText: "Ok" };

export function getLocations() {
  return new Promise((resolve) =>
    resolve(new Response(JSON.stringify(locations), responseOptions))
  );
}

export function addLocation(url) {
  const newLocation = {
    id: locations.length + 1,
    latitude: url.get("latitude"),
    longitude: url.get("longitude"),
    description: url.get("description"),
    subtitle: url.get("subtitle"),
    address: url.get("address"),
  };
  locations.push(newLocation);
  return new Promise((resolve) =>
    resolve(
      new Response(
        JSON.stringify({ status: "Add location successful" }),
        responseOptions
      )
    )
  );
}

export function deleteLocation(id) {
  const index = locations.findIndex((loc) => loc.id === id);
  if (index) locations.splice(index, 1);
  return new Promise((resolve) =>
    resolve(
      new Response(
        JSON.stringify({ status: "Delete location successful" }),
        responseOptions
      )
    )
  );
}

export function getProducts() {
  return new Promise((resolve) =>
    resolve(new Response(JSON.stringify(vegetables), responseOptions))
  );
}
